@import url('https://fonts.googleapis.com/css2?family=Almarai:wght@300&display=swap');
/* 'Almarai', sans-serif; */
@import url('https://fonts.googleapis.com/css2?family=Almarai:wght@300&family=Press+Start+2P&display=swap');
/* 'Press Start 2P', cursive; */
@import url('https://fonts.googleapis.com/css2?family=Almarai:wght@300&family=Press+Start+2P&family=Tangerine:wght@700&display=swap');
/* 'Tangerine', cursive; */


.text-box {
    background-image: url('/src/components/images/scroll-portrait-rotated.png');
    background-size: 100%;
    color: black;  
    height: 500px;
    width: 500px;
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
    align-items: center; 
    font-family: 'Tangerine', cursive;
    font-size: 30px;
}

.text {
    background-size: 100%;
    color: black; 
    width: 300px; 
    height:95px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    font-family: 'Tangerine', cursive;
    font-size: 30px;
}

.portrait-box {
    background-image: url('/src/components/images/scroll-portrait.png');
    background-size: 100%;
    color: black;  
    height: 500px;
    width: 500px;
    display: flex;
    flex-wrap:wrap;
    justify-content: center;
    align-items: center; 
    font-family: 'Tangerine', cursive;
    font-size: 30px;
    padding-top:30px;
}


.text-character{
    color: black;
    font-family: 'Tangerine', cursive;
    font-size: 30px;
    background-color: silver;
    height:35px;
    border-radius: 20px;
    text-align: center;
    width:200px;
    margin-right: 90px;
    margin-left: 90px;
    margin-top: 45px;
}